import React from 'react'
import './style.scss'
import Dialog from '../Dialog.js'
import { formatDateTime } from '../../../lib/utils'

const CheckDialog = props => {
  const { checkDialogVisible, data, closeDialog } = props

  return (
    <Dialog dialogVisible={checkDialogVisible} dialogTitle=''>
      <p className='dialog-content'>{formatDateTime(data.id)}</p>
      <p className='dialog-content'>{data.content}</p>
      <p className='dialog-content'>{data.completed ? '' : ''}</p>
      <button className='button button-primary confirm-button' onClick={closeDialog}>
        
      </button>
    </Dialog>
  )
}

export default CheckDialog
